#form, #modal__form {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

input {
  border-radius: 15px;
  margin-top: 15px;
  padding: 5px;
}

label {
  margin-top: 15px;
}

button {
  cursor: pointer;
  border: none;
  border-radius: 15px;
  margin-top: 15px;
  padding: 5px 10px;
  display: block;
}

.change__data {
  margin-top: 50px;
  margin-left: auto;
  margin-right: auto;
}

.delete__data {
  margin-left: auto;
  margin-right: auto;
}

select {
  cursor: pointer;
  border-radius: 15px;
  padding: 5px;
}

table {
  border-collapse: collapse;
  width: 90%;
  margin: 30px auto 20px;
}

th, td {
  text-align: center;
  border: 1px solid #ddd;
  padding: 10px;
}

th {
  background-color: #f2f2f2;
}

td:hover {
  background-color: #f5f5f5;
}

.backdrop-edit, .backdrop-delete {
  opacity: 1;
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
  z-index: 11;
  background-color: #0000003e;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  inset: 0;
  overflow: hidden;
}

.is-hidden-edit, .is-hidden-delete {
  opacity: 0;
  pointer-events: none;
}

.modal, .modal-student__delete {
  background-color: #fff;
  border-radius: 15px;
  width: 300px;
  height: auto;
  padding: 5px 20px 20px;
  position: absolute;
  top: 50%;
  right: 50%;
  overflow: hidden;
  transform: translate(50%, -50%);
}

.close-modal__button {
  justify-content: center;
  align-items: center;
  margin-left: auto;
  display: flex;
}

